<template>
  <div class="medicines-view">
    <h1>Просмотр лекарства</h1>
    <div v-if="loading">Загрузка...</div>
    <div v-else-if="error">{{ error }}</div>
    <div v-else>
      <p><strong>Название:</strong> {{ medicine.name }}</p>
      <p><strong>Категория:</strong> {{ medicine.category }}</p>
      <p><strong>Цена:</strong> {{ medicine.price }} ₽</p>
      <p><strong>Описание:</strong> {{ medicine.description }}</p>
      <p><strong>Производитель:</strong> {{ medicine.manufacturer }}</p>
      <p><strong>Изображение:</strong></p>
      <img :src="getAssetImage(medicine.image_url)" alt="Изображение лекарства" v-if="medicine.image_url" />
      <span v-else>Нет изображения</span>
      <p><strong>Дата истечения срока годности:</strong> {{ formatDate(medicine.expiration_date) }}</p>
      <p><strong>Противопоказания:</strong> {{ medicine.contraindications || "Нет данных" }}</p>
      <p><strong>Наличие:</strong> {{ medicine.availability ? 'В наличии' : 'Нет в наличии' }}</p>
    </div>
    <router-link to="/medicines">Назад к списку</router-link>
  </div>
</template>

<script>
import { medicinesApi } from '@/services/api'; // Используем medicinesApi

export default {
  data() {
    return {
      medicine: null,
      loading: true,
      error: null,
    };
  },
  async created() {
    await this.fetchMedicine();
  },
  methods: {
    async fetchMedicine() {
      try {
        const response = await medicinesApi.getMedicineById(this.$route.params.id);
        this.medicine = response.data;
        this.loading = false;
      } catch (error) {
        this.error = "Ошибка при загрузке лекарства";
        this.loading = false;
        console.error("Ошибка при загрузке лекарства:", error);
      }
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
    formatDate(dateString) {
      if (!dateString) return "Нет данных";
      const date = new Date(dateString);
      return date.toLocaleDateString("ru-RU");
    },
  },
};
</script>

<style scoped>
.medicines-view {
  margin: 20px;
}
img {
  max-width: 200px;
  height: auto;
  border-radius: 5px;
}
a {
  display: inline-block;
  margin-top: 20px;
  text-decoration: none;
  color: #007bff;
}
a:hover {
  text-decoration: underline;
}
</style>