<template>
  <div class="medical-good-create">
    <h1>Создание нового медицинского товара</h1>
    <form @submit.prevent="createMedicalGood">
      <div class="form-group">
        <label for="name">Название:</label>
        <input type="text" id="name" v-model="name" required />
      </div>

      <div class="form-group">
        <label for="category">Категория:</label>
        <input type="text" id="category" v-model="category" required />
      </div>

      <div class="form-group">
        <label for="price">Цена:</label>
        <input type="number" id="price" v-model="price" required />
      </div>

      <div class="form-group">
        <label for="description">Описание:</label>
        <textarea id="description" v-model="description"></textarea>
      </div>

      <div class="form-group">
        <label for="manufacturer">Производитель:</label>
        <input type="text" id="manufacturer" v-model="manufacturer" required />
      </div>

      <div class="form-group">
        <label for="image_url">URL изображения:</label>
        <input type="text" id="image_url" v-model="image_url" />
      </div>

      <div class="form-group">
        <label for="expiration_date">Дата истечения срока годности:</label>
        <input
          type="datetime-local"
          id="expiration_date"
          v-model="expiration_date"
          required
          :min="minDate"
          :max="maxDate"
        />
      </div>

      <div class="form-group">
        <label for="contraindications">Противопоказания:</label>
        <textarea id="contraindications" v-model="contraindications"></textarea>
      </div>

      <div class="form-group">
        <label for="availability">Наличие:</label>
        <select id="availability" v-model="availability" required>
          <option :value="true">В наличии</option>
          <option :value="false">Нет в наличии</option>
        </select>
      </div>

      <button type="submit" class="btn btn-primary">Создать</button>
      <router-link to="/medical-goods" class="btn btn-secondary">Отмена</router-link>
    </form>
  </div>
</template>

<script>
import { medicalGoodsApi } from '@/services/api'; // Импортируем medicalGoodsApi

export default {
  data() {
    const currentYear = new Date().getFullYear();
    const minDate = `${currentYear}-01-01T00:00`; // Минимальная дата: начало текущего года
    const maxDate = `${currentYear + 50}-12-31T23:59`; // Максимальная дата: конец года через 50 лет

    return {
      name: '', // Название товара
      category: '', // Категория
      price: null, // Цена (используем null для числовых полей)
      description: '', // Описание
      manufacturer: '', // Производитель
      image_url: '', // URL изображения
      expiration_date: '', // Дата истечения срока годности
      contraindications: '', // Противопоказания
      availability: true, // Наличие (по умолчанию true)
      minDate, // Минимальная дата
      maxDate, // Максимальная дата
    };
  },
  methods: {
    createMedicalGood() {
      const newMedicalGood = {
        name: this.name.trim(),
        category: this.category.trim(),
        price: parseFloat(this.price), // Преобразуем цену в число
        description: this.description.trim(),
        manufacturer: this.manufacturer.trim(),
        image_url: this.image_url.trim(),
        expiration_date: this.expiration_date,
        contraindications: this.contraindications.trim(),
        availability: this.availability, // Добавляем поле availability
      };

      // Проверка обязательных полей
      if (!newMedicalGood.name || !newMedicalGood.category || !newMedicalGood.price || !newMedicalGood.manufacturer || !newMedicalGood.expiration_date) {
        alert('Пожалуйста, заполните все обязательные поля.');
        return;
      }

      // Используем medicalGoodsApi.createMedicalGood
      medicalGoodsApi.addMedicalGood(newMedicalGood)
        .then(() => {
          alert('Медицинский товар успешно создан!');
          this.$router.push({ name: 'MedicalGoodsIndex' }); // Переход к списку товаров
        })
        .catch(error => {
          console.error('Ошибка при создании медицинского товара:', error);
          alert('Произошла ошибка при создании медицинского товара.');
        });
    },
  },
};
</script>

<style scoped>
.medical-good-create {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input,
textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.btn {
  padding: 10px 15px;
  margin-right: 10px;
}
</style>