<template>
  <div class="medicines-edit">
    <h1>Редактировать лекарство</h1>
    <form @submit.prevent="updateMedicine">
      <label for="name">Название:</label>
      <input type="text" id="name" v-model="formData.name" required />

      <label for="category">Категория:</label>
      <input type="text" id="category" v-model="formData.category" required />

      <label for="price">Цена:</label>
      <input type="number" id="price" v-model="formData.price" required />

      <label for="description">Описание:</label>
      <textarea id="description" v-model="formData.description"></textarea>

      <label for="manufacturer">Производитель:</label>
      <input type="text" id="manufacturer" v-model="formData.manufacturer" required />

      <label for="image_url">URL изображения:</label>
      <input type="text" id="image_url" v-model="formData.image_url" />

      <label for="expiration_date">Дата истечения срока годности:</label>
      <input type="datetime-local" id="expiration_date" v-model="formData.expiration_date" required />

      <label for="contraindications">Противопоказания:</label>
      <textarea id="contraindications" v-model="formData.contraindications"></textarea>

      <label for="availability">Наличие:</label>
      <select id="availability" v-model="formData.availability" required>
        <option :value="true">В наличии</option>
        <option :value="false">Нет в наличии</option>
      </select>

      <button type="submit">Сохранить</button>
    </form>
  </div>
</template>

<script>
import { medicinesApi } from '@/services/api'; // Используем medicinesApi

export default {
  data() {
    return {
      formData: {
        name: '', // Название лекарства
        category: '', // Категория
        price: null, // Цена
        description: '', // Описание
        manufacturer: '', // Производитель
        image_url: '', // URL изображения
        expiration_date: '', // Дата истечения срока годности
        contraindications: '', // Противопоказания
        availability: true, // Наличие (по умолчанию true)
      },
    };
  },
  async created() {
    await this.fetchMedicine();
  },
  methods: {
    async fetchMedicine() {
      try {
        const response = await medicinesApi.getMedicineById(this.$route.params.id);
        this.formData = response.data;
      } catch (error) {
        console.error("Ошибка при загрузке лекарства:", error);
      }
    },
    async updateMedicine() {
      try {
        await medicinesApi.updateMedicine(this.$route.params.id, this.formData);
        this.$router.push("/medicines");
      } catch (error) {
        console.error("Ошибка при обновлении лекарства:", error);
      }
    },
  },
};
</script>

<style scoped>
.medicines-edit {
  margin: 20px;
}
form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
label {
  font-weight: bold;
}
input, textarea {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: #0056b3;
}
</style>