<template>
  <div class="medicine-create">
    <h1>Создание нового лекарства</h1>
    <form @submit.prevent="createMedicine">
      <div class="form-group">
        <label for="name">Название:</label>
        <input type="text" id="name" v-model="name" required />
      </div>

      <div class="form-group">
        <label for="category">Категория:</label>
        <input type="text" id="category" v-model="category" required />
      </div>

      <div class="form-group">
        <label for="price">Цена:</label>
        <input type="number" id="price" v-model="price" required />
      </div>

      <div class="form-group">
        <label for="description">Описание:</label>
        <textarea id="description" v-model="description"></textarea>
      </div>

      <div class="form-group">
        <label for="manufacturer">Производитель:</label>
        <input type="text" id="manufacturer" v-model="manufacturer" required />
      </div>

      <div class="form-group">
        <label for="image_url">URL изображения:</label>
        <input type="text" id="image_url" v-model="image_url" />
      </div>

      <div class="form-group">
        <label for="expiration_date">Дата истечения срока годности:</label>
        <input type="datetime-local" id="expiration_date" v-model="expiration_date" required />
      </div>

      <div class="form-group">
        <label for="contraindications">Противопоказания:</label>
        <textarea id="contraindications" v-model="contraindications"></textarea>
      </div>

      <div class="form-group">
        <label for="availability">Наличие:</label>
        <select id="availability" v-model="availability" required>
          <option :value="true">В наличии</option>
          <option :value="false">Нет в наличии</option>
        </select>
      </div>

      <button type="submit" class="btn btn-primary">Создать</button>
      <router-link to="/medicines" class="btn btn-secondary">Отмена</router-link>
    </form>
  </div>
</template>
  
<script>
import { medicinesApi } from '@/services/api';

export default {
  data() {
    return {
      name: '', // Название лекарства
      category: '', // Категория
      price: '', // Цена
      description: '', // Описание
      manufacturer: '', // Производитель
      image_url: '', // URL изображения
      expiration_date: '', // Дата истечения срока годности
      contraindications: '', // Противопоказания
      availability: true, // Наличие (по умолчанию true)
    };
  },
  methods: {
    createMedicine() {
      const newMedicine = {
        name: this.name,
        category: this.category,
        price: this.price,
        description: this.description,
        manufacturer: this.manufacturer,
        image_url: this.image_url,
        expiration_date: this.expiration_date,
        contraindications: this.contraindications,
        availability: this.availability, // Добавляем поле "Наличие"
      };

      medicinesApi.addMedicine(newMedicine)
        .then(() => {
          alert('Лекарство успешно создано!');
          this.$router.push({ name: 'MedicinesIndex' });
        })
        .catch(error => {
          console.error('Ошибка при создании лекарства:', error);
          alert('Произошла ошибка при создании лекарства.');
        });
    }
  }
};
</script>
  <style scoped>
  .medicine-create {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
  }
  
  .btn {
    padding: 10px 15px;
    margin-right: 10px;
  }
  </style>