import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue'; // Главная страница
import AboutView from '../views/AboutView.vue'; // Страница "О нас"
import ContactsView from '../views/ContactsView.vue'; // Страница "Контакты"
import MedicalGoodsList from "../views/MedicalGoodsList.vue";
import MedicalGoodsAdd from "../views/MedicalGoodsAdd.vue";
import MedicalGoodsEdit from "../views/MedicalGoodsEdit.vue";
import MedicalGoodsView from "../views/MedicalGoodsView.vue";
import MedicinesList from "../views/MedicinesList.vue";
import MedicinesAdd from "../views/MedicinesAdd.vue";
import MedicinesEdit from "../views/MedicinesEdit.vue";
import MedicinesView from "../views/MedicinesView.vue";

const routes = [
  { path: '/', name: 'Home', component: HomeView },
  { path: '/about', name: 'About', component: AboutView },
  { path: '/contacts', name: 'Contacts', component: ContactsView },
  { path: "/medical-goods", name: 'MedicalGoodsIndex', component: MedicalGoodsList },
  { path: "/medical-goods/add", name: 'MedicalGoodCreate', component: MedicalGoodsAdd },
  { path: "/medical-goods/edit/:id", name: 'MedicalGoodEdit', component: MedicalGoodsEdit },
  { path: "/medical-goods/view/:id", name: 'MedicalGoodsView', component: MedicalGoodsView },
  { path: "/medicines", name: 'MedicinesIndex', component: MedicinesList },
  { path: "/medicines/add", name: 'MedicineCreate', component: MedicinesAdd },
  { path: "/medicines/edit/:id", name: 'MedicineEdit', component: MedicinesEdit },
  { path: "/medicines/view/:id", name: 'MedicineView', component: MedicinesView }, // Исправлено имя маршрута
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;