import axios from 'axios';

const apiClient = axios.create({
  baseURL: 'http://localhost:5000/api', // Адрес вашего бэкенда
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Методы для работы с таблицей medical_goods
export const medicalGoodsApi = {
  getMedicalGoods(page = 1, limit = 10) {
    return apiClient.get(`/medical-goods`, {
      params: { page, limit },
    });
  },
  getMedicalGoodById(id) {
    return apiClient.get(`/medical-goods/${id}`);
  },
  addMedicalGood(data) {
    return apiClient.post('/medical-goods', data);
  },
  updateMedicalGood(id, data) {
    return apiClient.put(`/medical-goods/${id}`, data);
  },
  deleteMedicalGood(id) {
    return apiClient.delete(`/medical-goods/${id}`);
  },
};

