<template>
  <div class="medicine-index">
    <h1>Список лекарств</h1>
    <router-link to="/medicines/add" class="btn btn-primary">Добавить лекарство</router-link>
    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Название</th>
          <th>Категория</th>
          <th>Цена</th>
          <th>Производитель</th>
          <th>Наличие</th>
          <th>Изображение</th>
          <th>Действия</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="medicine in medicines" :key="medicine.id">
          <td>{{ medicine.id }}</td>
          <td>{{ medicine.name }}</td>
          <td>{{ medicine.category }}</td>
          <td>{{ medicine.price }} ₽</td>
          <td>{{ medicine.manufacturer }}</td>
          <td>
            <span :class="medicine.availability ? 'availability-true' : 'availability-false'">
              {{ medicine.availability ? 'В наличии' : 'Нет в наличии' }}
            </span>
          </td>
          <td>
            <img
              v-if="medicine.image_url"
              :src="getAssetImage(medicine.image_url)"
              :alt="medicine.name"
              style="width: 100px; height: auto;"
            />
            <span v-else>Нет изображения</span>
          </td>
          <td>
            <router-link :to="{ name: 'MedicineView', params: { id: medicine.id } }" class="btn btn-info">
              Просмотр
            </router-link>
            <router-link :to="{ name: 'MedicineEdit', params: { id: medicine.id } }" class="btn btn-warning">
              Редактировать
            </router-link>
            <button @click="deleteMedicine(medicine.id)" class="btn btn-danger">Удалить</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Пагинация -->
    <div class="pagination">
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="btn btn-secondary"
      >
        Предыдущая
      </button>
      <span>Страница {{ currentPage }} из {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="btn btn-secondary"
      >
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import { medicinesApi } from '@/services/api';

export default {
  data() {
    return {
      medicines: [], // Список лекарств
      currentPage: 1, // Текущая страница
      limit: 10, // Количество элементов на странице
      total: 0, // Общее количество лекарств
      totalPages: 0, // Общее количество страниц
    };
  },
  created() {
    this.fetchMedicines(); // Получаем список лекарств при создании компонента
  },
  methods: {
    fetchMedicines() {
      medicinesApi.getMedicines(this.currentPage, this.limit)
        .then(response => {
          console.log('Response from server:', response); // Логируем ответ сервера
          if (!response.data || !response.data.meta) {
            throw new Error('Invalid response format from server');
          }
          this.medicines = response.data.data;
          this.total = response.data.meta.total;
          this.totalPages = response.data.meta.totalPages;
        })
        .catch(error => {
          console.error('Ошибка при получении списка лекарств:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    changePage(page) {
      if (page < 1 || page > this.totalPages) return;
      this.currentPage = page;
      this.fetchMedicines(); // Обновляем данные при смене страницы
    },
    deleteMedicine(id) {
      if (!confirm('Вы уверены, что хотите удалить это лекарство?')) {
        return;
      }

      medicinesApi.deleteMedicine(id)
        .then(() => {
          this.medicines = this.medicines.filter(medicine => medicine.id !== id);
          alert('Лекарство успешно удалено!');
        })
        .catch(error => {
          console.error('Ошибка при удалении лекарства:', error);
          alert(`Произошла ошибка при удалении лекарства: ${error.message}`);
        });
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
.table img {
  width: 50px;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.btn {
  margin-right: 5px;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 14px;
}
.pagination span {
  margin: 0 10px;
  font-size: 16px;
}
.availability-true {
  color: green;
  font-weight: bold;
}
.availability-false {
  color: red;
  font-weight: bold;
}
</style>