<template>
  <div class="medical-good-view">
    <h1>Просмотр медицинского товара</h1>
    <div v-if="good">
      <h2>{{ good.name }}</h2>
      <p><strong>ID:</strong> {{ good.id }}</p>
      <p><strong>Категория:</strong> {{ good.category }}</p>
      <p><strong>Цена:</strong> {{ good.price }} ₽</p>
      <p><strong>Производитель:</strong> {{ good.manufacturer }}</p>
      <p><strong>Описание:</strong> {{ good.description }}</p>
      <p><strong>Дата истечения срока годности:</strong> {{ formatDate(good.expiration_date) }}</p>
      <p><strong>Противопоказания:</strong> {{ good.contraindications }}</p>
      <p><strong>Наличие:</strong> {{ good.availability ? 'В наличии' : 'Нет в наличии' }}</p>
      <img
        v-if="good.image_url"
        :src="getAssetImage(good.image_url)"
        :alt="good.name"
        style="width: 200px; height: auto;"
      />
      <span v-else>Нет изображения</span>
    </div>
    <div v-else>
      <p>Загрузка данных...</p>
    </div>
    <router-link to="/medical-goods" class="btn btn-secondary">Назад к списку</router-link>
  </div>
</template>

<script>
import { medicalGoodsApi } from '@/services/api';

export default {
  data() {
    return {
      good: null, // Данные о товаре
    };
  },
  created() {
    this.fetchGood(); // Получаем данные о товаре при создании компонента
  },
  methods: {
    fetchGood() {
      const id = this.$route.params.id; // Получаем ID из параметров маршрута
      medicalGoodsApi.getMedicalGoodById(id)
        .then(response => {
          this.good = response.data;
        })
        .catch(error => {
          console.error('Ошибка при получении данных о товаре:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString();
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
  },
};
</script>

<style scoped>
.medical-good-view {
  margin: 20px;
}
img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}
.btn {
  margin-top: 20px;
}
</style>