<template>
  <div class="medical-goods-index">
    <h1>Список медицинских товаров</h1>
    <router-link to="/medical-goods/add" class="btn btn-primary">Добавить товар</router-link>
    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Название</th>
          <th>Категория</th>
          <th>Цена</th>
          <th>Производитель</th>
          <th>Наличие</th>
          <th>Противопоказания</th>
          <th>Изображение</th>
          <th>Действия</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="good in goods" :key="good.id">
          <td>{{ good.id }}</td>
          <td>{{ good.name }}</td>
          <td>{{ good.category }}</td>
          <td>{{ good.price }} ₽</td>
          <td>{{ good.manufacturer }}</td>
          <td>
            <span :class="['availability', good.availability ? 'available' : 'unavailable']">
              {{ good.availability ? 'В наличии' : 'Нет в наличии' }}
            </span>
          </td>
          <td>{{ good.contraindications || 'Нет данных' }}</td>
          <td>
            <img
              v-if="good.image_url"
              :src="getAssetImage(good.image_url)"
              :alt="good.name"
              style="width: 100px; height: auto;"
            />
            <span v-else>Нет изображения</span>
          </td>
          <td>
            <router-link :to="{ name: 'MedicalGoodsView', params: { id: good.id } }" class="btn btn-info">
              Просмотр
            </router-link>
            <router-link :to="{ name: 'MedicalGoodEdit', params: { id: good.id } }" class="btn btn-warning">
              Редактировать
            </router-link>
            <button @click="deleteGood(good.id)" class="btn btn-danger">Удалить</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Пагинация -->
    <div class="pagination">
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="btn btn-secondary"
      >
        Предыдущая
      </button>
      <span>Страница {{ currentPage }} из {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="btn btn-secondary"
      >
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import { medicalGoodsApi } from '@/services/api';

export default {
  data() {
    return {
      goods: [], // Список медицинских товаров
      currentPage: 1, // Текущая страница
      limit: 10, // Количество элементов на странице
      total: 0, // Общее количество товаров
      totalPages: 0, // Общее количество страниц
    };
  },
  created() {
    this.fetchGoods(); // Получаем список товаров при создании компонента
  },
  methods: {
    fetchGoods() {
      medicalGoodsApi.getMedicalGoods(this.currentPage, this.limit)
        .then(response => {
          console.log('Response from server:', response); // Логируем ответ сервера
          if (!response.data || !response.data.meta) {
            throw new Error('Invalid response format from server');
          }
          this.goods = response.data.data;
          this.total = response.data.meta.total;
          this.totalPages = response.data.meta.totalPages;
        })
        .catch(error => {
          console.error('Ошибка при получении списка товаров:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    changePage(page) {
      if (page < 1 || page > this.totalPages) return;
      this.currentPage = page;
      this.fetchGoods(); // Обновляем данные при смене страницы
    },
    deleteGood(id) {
      if (!confirm('Вы уверены, что хотите удалить этот товар?')) {
        return;
      }

      medicalGoodsApi.deleteMedicalGood(id)
        .then(() => {
          this.goods = this.goods.filter(good => good.id !== id);
          alert('Товар успешно удален!');
        })
        .catch(error => {
          console.error('Ошибка при удалении товара:', error);
          alert(`Произошла ошибка при удалении товара: ${error.message}`);
        });
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
.table img {
  width: 50px;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.btn {
  margin-right: 5px;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 14px;
}
.pagination span {
  margin: 0 10px;
  font-size: 16px;
}

/* Стили для поля "Наличие" */
.availability {
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;
}
.available {
  color: #fff;
  background-color: green;
}
.unavailable {
  color: #fff;
  background-color: red;
}
</style>